export default {
	data() {
		return {
			
		}  
	},
	methods: {
	   formSubmit: function(e) {
		   const that = this;
		   const vs = e.detail.value;
		   that.$http.requestPost({
			   url:`${that.$baseUrl}/api/user/register`,
			   data: vs,
			   success: res=>{
				     uni.showToast({
						title: res.msg,
						icon:'none',
						duration: 2000
					});
				     uni.navigateTo({
				    	url: "/pages/login/login"
				    })
			   }
		   })
	   },
	   toLogin: function() {
		   uni.navigateTo({
			  url:"/pages/login/login"
		   })
	   }
	}
}
